* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

h1,
h2,
p {
  margin: 0;
  padding: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.basic-info {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.country-img {
  width: 40px;
  height: 30px;
  margin-right: 10px;
}

.country-name {
  font-size: 44px;
  font-weight: 700;
}

.country-name-list {
  font-size: 20px;
  font-weight: 600;
}

.country-info {
  font-weight: 600;
  font-size: 24px;
}

.country-info:not(:last-child) {
  margin-bottom: 10px;
}

.country-span {
  font-weight: 400;
  font-size: 20px;
}

.country-item {
  display: flex;
  align-items: center;
}

.country-item:not(:last-child) {
  margin-bottom: 10px;
}
